.outer-create-container {
  display: flex;
  justify-content: center;
  margin: 30px 0 30px 0;
}

.new-deck {
  width: 60%;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.4), 0 10px 10px rgba(0, 0, 0, 0.22);
  height: auto;
}

.new-deck-form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 40px 20px;
}

.new-deck h1 {
  color: #fff;
}

#deckName {
  outline-color: indigo;
  margin: 0px;
  height: 47px;
  font-size: 16px;
  border: hsl(0, 0%, 80%) solid 1px;
  background-color: white;
}

.new-deck-form textarea {
  font-size: 15px;
  width: 100%;
  min-height: 160px;
  resize: none;
  padding: 10px;
  outline-color: indigo;
}

#new-deck-button {
  margin: 0;
  text-align: center;
  width: 140px;
  text-transform: uppercase;
  align-self: flex-end;
  border-radius: 20px;
  border: 1px solid #570a7bbc;
  background-color: #7818a4;
  color: #ffffff;
  height: 42px;
  font-size: 12px;
  font-weight: bold;
  letter-spacing: 1px;
  cursor: pointer;
  transition: transform 80ms ease-in;
}

.banner {
  width: 100%;
  height: 100px;
  background-color: #5f0a87;
  background-image: linear-gradient(326deg, #7818a4 20%, #5f0a87 80%);
  padding: 4% 20px;
  clip-path: polygon(0 0, 100% 0, 100% 85%, 0 100%);
  -webkit-backface-visibility: hidden;
  -webkit-transform: scale(1);
}

.bottom-banner {
  height: 60px;
  width: 100%;
  background-color: #5f0a87;
  background-image: linear-gradient(326deg, #7818a4 20%, #5f0a87 80%);
  clip-path: polygon(0 15%, 100% 0, 100% 100%, 0 100%);
}

@media screen and (max-width: 1200px) {
  .outer-create-container {
    padding: 20px;
  }
  .new-deck {
    width: 100%;
  }
}

@media screen and (max-width: 800px) {
  .outer-create-container {
    padding: 20px;
  }

  .new-deck-page {
    margin-left: 0;
    padding-top: 120px;
  }

  .new-deck {
    min-height: 100%;
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    border-radius: 8px;
    width: 100%;
  }
}
